import React, { lazy } from "react";
import { Link as Scroll } from 'react-scroll';

/** Material UI Modules */
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Modal from '@mui/material/Modal';
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";

const Contact = lazy(() => import("components/organisms/Contact"));

const Header = () => {
  // 
  const [hasOpenContact, setHasOpenContact] = React.useState(false);
  const handleContactOpen = () => setHasOpenContact(true);
  // 
  const handleContactClose = (event, reason) => {
    if (reason !== 'backdropClick') {
      setHasOpenContact(false);
    }
  }

  return (
    <>
      <Box
        sx={{
          flexGrow: 1
        }}
      >
        <AppBar
          sx={{
            background: "white"
          }}
          position="fixed"
          elevation={0}
        >
          <Toolbar>
            {/**  */}
            <Typography
              sx={{
                flexGrow: 1,
                ml: 15
              }}
              variant="h5"
              color="black"
            >
              <Scroll
                to="top"
                smooth={true}
                duration={600}
                offset={-64}
              >
                SuiSuis Portfolio
              </Scroll>
            </Typography>
            {/**  */}
            <Button
              sx={{
                background: "white",
                color: "gray",
                mr: 1,
                "&:hover": {
                  backgroundColor: "white",
                  color: "black"
                }
              }}
            >
              <Scroll
                to="about"
                smooth={true}
                duration={600}
                offset={-64}
              >
                
              </Scroll>
            </Button>
            {/**  */}
            <Button
              sx={{
                background: "white",
                color: "gray",
                mr: 1,
                ml: 1,
                "&:hover": {
                  backgroundColor: "white",
                  color: "black"
                }
              }}
            >
              <Scroll
                to="skill"
                smooth={true}
                duration={600}
                offset={-64}
              >
                
              </Scroll>
            </Button>
            {/**  */}
            <Button
              sx={{
                background: "white",
                color: "gray",
                mr: 1,
                ml: 1,
                "&:hover": {
                  backgroundColor: "white",
                  color: "black"
                }
              }}
            >
              <Scroll
                to="work"
                smooth={true}
                duration={600}
                offset={-64}
              >
                
              </Scroll>
            </Button>
            {/**  */}
            <Button
              sx={{
                background: "white",
                color: "gray",
                mr: 15,
                ml: 1,
                "&:hover": {
                  backgroundColor: "white",
                  color: "black"
                }
              }}
              onClick={handleContactOpen}
            >
              
            </Button>
            {/**  */}
            <Modal
              open={hasOpenContact}
              onClose={handleContactClose}
              aria-labelledby="modal-contact"
              aria-describedby="modal-contact"
              disableEnforceFocus
            >
              <Contact
                setHasOpenContact={setHasOpenContact}
              />
            </Modal>
          </Toolbar>
        </AppBar>
        <Toolbar />
      </Box>
    </>
  );
}

export default Header;
